@import url(https://fonts.googleapis.com/css2?family=Barlow+Condensed:wght@400;500;700&display=swap);
#containerMenu{
    position:fixed;
    top:0;
    width:100%;
    z-index: 999;
}
#listaCat{
    list-style-type: none;
    margin: 0;
    padding: 0;
    overflow: hidden;
    background-color: white;
}
#logo{
    float: left;
    width: 15%;
    height: 63px;
    overflow: hidden;
    margin-left: 1%;
    margin-top:0.1%;
}

#logo img {
    margin-top:1%;
    transform:scale(1.7,1.7);
    object-fit:cover;
    height:80%;
    width:90%;
    margin-left:14%;
}
.liCFilho {
    float: left;
    margin-left: 3%;
}
.liCFilho div {
    display: flex;
    text-align: center;
    padding: 14px;
    text-decoration: none;
    -webkit-font-smoothing: antialised;
    font: 500 20px Barlow Condensed, sans-serif;
    color: black;
}
li div:hover{
    cursor: pointer;
}
.listaCat2{
    list-style-type: none;
    overflow: hidden;
    background-color: white;
    display: none;
    position: absolute;
    margin-right:0;
    padding: 0;
    height: 150px;
    overflow-y: auto;
    
}
.listaCat2 li{
    margin-top: 2%;
    width: 150px;
    color: rgb(54, 54, 54);
    -webkit-font-smoothing: antialised;
    font: 500 20px Barlow Condensed, sans-serif;
}
.listaCat2 li p{
    padding:5%;
    margin-left: 5%;
}
#logar div:hover{
    cursor: auto;
    background-color: white;
}
.listaCat2 li:hover{
    cursor: pointer;
    background-color: rgba(0, 0, 0, 0.2);
}
.liCFilho:hover{
    background-color: rgba(0, 0, 0, 0.2);
}
.liCFilho:hover .listaCat2{
    display: block;
}
